.sticky-top {
  position: fixed !important;
  top: 20px !important;
  bottom: unset !important;
  left: unset;
  right: unset;
  z-index: 120;
}
.sticky-bottom {
  position: fixed !important;
  bottom: 20px !important;
  top: unset !important;
  left: unset;
  right: unset;
  z-index: 120;
}
.sticky-free {
  position: relative !important;
  top: unset !important;
  bottom: unset !important;
}
